/**
 * Schm model events
 */

'use strict';

import {EventEmitter} from 'events';
import Schm from './schm.model';
var SchmEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
SchmEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  Schm.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    SchmEvents.emit(event + ':' + doc._id, doc);
    SchmEvents.emit(event, doc);
  }
}

export default SchmEvents;
